@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
.App {
  text-align: center;
  min-height: 95vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer {
  text-align: center;
  margin-top: 20px;
}

ul {
  list-style: none;
}
ul li {
  text-decoration: none;
}

.JokeDisplay_box__A7p0i {
  background-color: #44bd32;
  color: #192a56;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  width: 600px;
  max-width: 100%;
  text-align: center;
}

.JokeDisplay_box__A7p0i button {
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  background-color: #273c75;
  border: 0;
  color: white;
  border-radius: 5px;
  padding: 0.6rem;
  font-family: inherit;
  cursor: pointer;
  transition: transform 0.2s ease-in;
  align-items: center;
}
.JokeDisplay_box__A7p0i button:active {
  transform: scale(0.9);
}
.JokeDisplay_box__A7p0i button:focus {
  outline: none;
}

.JokeDisplay_box__A7p0i h2 {
  margin-top: 0;
}
.JokeDisplay_text__2Otit {
  margin: 2rem;
}

.Categories_inputSelect__2UZTo {
  font-family: inherit;
  outline: none;
  max-width: 100%;
  width: 150px;
  color: blue;
  height: 5vh;
  line-height: 1.3;
  font-weight: 800;
  padding: 0.6em 1.4em 0.5em 0.8em;
}

.Categories_inputSelectArea__2Wa3O {
